/** @format */
* {
  margin: 0;
}
*::-webkit-scrollbar {
  display: none;
}
.div_stografo_logo_text {
  width: 33.33%;
  float: left;
}
.stografo_logo_text {
  width: 40%;
}
ul {
  width: 33.33%;
  background-color: blue;
  margin-top: 10px;
  float: left;
  text-align: center;
}
li {
  margin: 0px 0px 0 0px;
  width: 25%;
  display: inline-block;
  font-size: 8px;
  padding: 15px 0 12px 0;
}
li:hover {
  color: #00374b;
  cursor: pointer;
}
.div_instagramLogo {
  margin-top: 10px;
  margin-right: 15px;
  width: 60px;
  float: right;
}
.instagramLogo {
  width: 50px;
  height: 50px;
  float: right;
}
